import * as React from 'react';
import { Spinner, SpinnerType } from 'office-ui-fabric-react';

import { IPlaceholderSpinnerProps } from './IPlaceholderProps';
import './Placeholder.scss';

/**
 * Component used to display place holder content in the web part display area.
 *
 * @alpha
 */
/* tslint:disable:no-any */ // No state yet
export default class PlaceholderSpinner extends React.Component<IPlaceholderSpinnerProps, void> {
/* tslint:enable:no-any */

  /**
   * Render the place holder user experience.
   */
  public render(): React.ReactElement<IPlaceholderSpinnerProps> {
    return (
      <div className='Placeholder'>
        <div className='Placeholder-spinnerContainer'>
          <Spinner type={ SpinnerType.large }  label={ this.props.label }/>
        </div>
      </div>
    );
  }
}